import {countadd,CHANGECITY} from "./action"



const defaultstate={
    count:2018
}
export default (state=defaultstate,action)=>{
  switch(action.type){
      case "COUNTADD":
      console.log(state)
         state.count+=10;
         return state;
      break;
      case"CHANGECITY":
         return action.city
      default:
        return state;
      break;
  }



}


